@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');
.main_top{
    text-decoration: none;
    width: 100%;
    height: auto;
    margin-top: 40px;
}
.news_box{
    width: 100%;
}
.news_box h2{
    font-size: 36px;
    font-weight: 500;
    font-family: 'Raleway', sans-serif;
    color: #06456A;
}
.content{
    width: 370px;
    height: 350px;
    background: url('../Img/Content_img.png') no-repeat;
    background-size: contain;
    margin: 0;
    padding: 0;
}
.content a{
    text-decoration: none;
}
.content_news{
    width: 170px;
    height: 30px;
    border-radius: 4px;
    background: #06456A;
    text-align: center;
    padding-top: 5px;
}
.content_news p, .content_time p{
    font-weight: 600;
    font-size: 12px;
    color: white;
    font-family: 'Inter', sans-serif;
}
.content_text{
    width: 90%;
    height: 60px;
    margin-top: 50%;
}
.content_text p{
    font-weight: 600;
    font-size: 18px;
    color: white;
    font-family: 'Inter', sans-serif;
}
.content_time p{
    margin-bottom: 0;
}
.quick{
    margin: 0;
    padding: 0;
}
.quick_news{
    width: 100%;
}
.quick_news img{
    width: 60%;
}
.quick_text p{
    font-size: 15px;
    font-weight: 500;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0;
    color: #06456A;
}
.quick_text .text_2{
    color: #000;
    font-size: 10px;
}
.quick_text a{
    color: #7D7D7D;
    text-decoration: none;
}

.quick_text img{
    width: 50%;
}

.main_section{
    margin-top: 40px;
}
.swiper{
    height: 300px;
    margin-top: 50px;
    padding: 100px 30px;
}
.swiper a{
    text-decoration: none;
    height: 300px;
}
.swiper-container{
    height: 200px;
}