@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500&display=swap');
.header{
    width: 100%;
    height: auto;
    font-weight: normal;
    font-family: 'Raleway', sans-serif;
}
.row-first{
    width: 100%;
    height: 70px;
    background: #06456A;
}
.search input{
    width: 235px;
    height: 30px;
    background: #fff;
    padding: 10px;
    font-weight: normal;
    font-size: 12px;
    color: #000;
    border: none;
    border-radius: 32px;
    line-height: 15px;
}
.icon{
    background: none;
    color: white;
}
.row-second{
    padding-top: 15px;
    height: auto;
}
.logo1{
    height: 50%;
    padding-top: 10px;
}
.navbar{
    background: #06456A;
    height: 70px;
}
.navbar ul{
    width: 100%;
    margin-top: 13px;
}
.navbar ul li a{
    color: white;
    font-weight: 600;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
}
.navbar ul li a:hover{
    color: rgb(177, 177, 177);
}
.logo2{
    margin-bottom: 10px;
}