@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}
.header{
    width: 100%;
    height: auto;
    font-weight: normal;
    font-family: 'Raleway', sans-serif;
}
.row-first{
    width: 100%;
    height: 70px;
    background: #06456A;
}
.search input{
    width: 235px;
    height: 30px;
    background: #fff;
    padding: 10px;
    font-weight: normal;
    font-size: 12px;
    color: #000;
    border: none;
    border-radius: 32px;
    line-height: 15px;
}
.icon{
    background: none;
    color: white;
}
.row-second{
    padding-top: 15px;
    height: auto;
}
.logo1{
    height: 50%;
    padding-top: 10px;
}
.navbar{
    background: #06456A;
    height: 70px;
}
.navbar ul{
    width: 100%;
    margin-top: 13px;
}
.navbar ul li a{
    color: white;
    font-weight: 600;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
}
.navbar ul li a:hover{
    color: rgb(177, 177, 177);
}
.logo2{
    margin-bottom: 10px;
}
.main_top{
    text-decoration: none;
    width: 100%;
    height: auto;
    margin-top: 40px;
}
.news_box{
    width: 100%;
}
.news_box h2{
    font-size: 36px;
    font-weight: 500;
    font-family: 'Raleway', sans-serif;
    color: #06456A;
}
.content{
    width: 370px;
    height: 350px;
    background: url(/static/media/Content_img.46a2f4b1.png) no-repeat;
    background-size: contain;
    margin: 0;
    padding: 0;
}
.content a{
    text-decoration: none;
}
.content_news{
    width: 170px;
    height: 30px;
    border-radius: 4px;
    background: #06456A;
    text-align: center;
    padding-top: 5px;
}
.content_news p, .content_time p{
    font-weight: 600;
    font-size: 12px;
    color: white;
    font-family: 'Inter', sans-serif;
}
.content_text{
    width: 90%;
    height: 60px;
    margin-top: 50%;
}
.content_text p{
    font-weight: 600;
    font-size: 18px;
    color: white;
    font-family: 'Inter', sans-serif;
}
.content_time p{
    margin-bottom: 0;
}
.quick{
    margin: 0;
    padding: 0;
}
.quick_news{
    width: 100%;
}
.quick_news img{
    width: 60%;
}
.quick_text p{
    font-size: 15px;
    font-weight: 500;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0;
    color: #06456A;
}
.quick_text .text_2{
    color: #000;
    font-size: 10px;
}
.quick_text a{
    color: #7D7D7D;
    text-decoration: none;
}

.quick_text img{
    width: 50%;
}

.main_section{
    margin-top: 40px;
}
.swiper{
    height: 300px;
    margin-top: 50px;
    padding: 100px 30px;
}
.swiper a{
    text-decoration: none;
    height: 300px;
}
.swiper-container{
    height: 200px;
}
.footer{
    width: 100%;
    height: 450px;
    padding: 50px;
    background: #06456A;
    margin-top: 50px;
}
.footer_h2{
    font-family: 'Inter', sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 39px;
    letter-spacing: 0em;
    text-align: left;
    color: white ;
}
.phone{
    font-size: 26px;
    color: white;
}
.footer_text p, .footer_text a{
    font-family: 'Inter';
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
    color: white;
    margin-top: 30px;
    text-decoration: none;
}
.footer_menu {
    height: 250px;
}
.footer_menu li a{
    font-family: 'Inter';
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
    text-decoration: none;
    color: white ;
    margin-top: 25px;
}
